import React, { useState, useEffect, useRef } from 'react';
import { useNuiEvent } from "../../../hooks/useNuiEvent";
import { fetchNui } from '../../../utils/fetchNui';
import './index.css'

const VehicleSpawner = () => {
  const [shouldShow, setShouldShow] = useState(true)
  const [vehicles, setVehicles] = useState([]);
  const [filteredVehicles, setFilteredVehicles] = useState([])

  useNuiEvent<any>('vehiclespawner', ( data ) => {
      setShouldShow(true)
      setVehicles(data.cars)
      setFilteredVehicles(data.cars)
    })

    useEffect(() => {
      if (shouldShow) {
        window.addEventListener('keydown', handleEscapeKey);
      } else {
        window.removeEventListener('keydown', handleEscapeKey);
      }
  
      return () => {
        window.removeEventListener('keydown', handleEscapeKey);
      };
    }, [shouldShow]);

    const handleEscapeKey = (event) => {
      if (event.key === 'Escape') {
        setShouldShow(false);
        fetchNui('carspawner:closeNUI', {})
      }
    };

    const searchVehicles = (searchValue) => {
      if (searchValue !== '') {
        const filteredVehicles = vehicles.filter((item) => {
          return item.model.toLowerCase().startsWith(searchValue.toLowerCase());
        })
        setFilteredVehicles(filteredVehicles)
      } else {
        setFilteredVehicles(vehicles)
      }
    }

    const handleUseVehicle = (vehicle) => {
      //console.log(vehicle)
      setShouldShow(false)
      fetchNui('carspawner:spawnVehicle', { MODEL: vehicle})
    }

  return (
    <div className="container" style={{ display: shouldShow ? '' : 'none' }}>
      {filteredVehicles.map((vehicle, index) => (
              <div className="card">
                <h3>{vehicle.model}</h3>
              <img
                src={vehicle.image}
                alt="Vehicle Image"
              />
              <button onClick={() => handleUseVehicle(vehicle.model)}>Spawn</button>
            </div>
      ))}
    </div>
  );
};

export default VehicleSpawner;