import React from 'react';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import './index.css'
import Application from '../Application';
import Taskbar from './taskbar/components/taskbar';
import KillFeed from './killfeed/components/killfeed';
import VehicleSpawner from './vehicleSpawner/components/vSpawner';

const darkTheme = createTheme({
  components: {
    MuiMenuItem: {
      styleOverrides: {
        root: {
          backgroundColor: "#2f2f2f",
          "&.Mui-selected": {
            backgroundColor: "rgba(255, 255, 255, 0.08)",
            "&.Mui-focusVisible": { background: "rgba(0, 0, 0, 0.24)" }
          },
          "&.Mui-selected:hover": {
            backgroundColor: "rgba(255, 255, 255, 0.08)",
          }
        }
      }
    },
    MuiCircularProgress: {
      styleOverrides: {
        circle: {
          strokeLinecap: 'butt'
        }
      }
    },
    MuiInput: {
      styleOverrides: {
        root: {
          "& .MuiInput-root": {
            color: "white",
            fontSize: '1.3vmin'
          },
          "& label.Mui-focused": {
            color: "darkgray"
          },
          "& .MuiInput-underline:hover:not(.Mui-disabled):before": {
            borderColor: "darkgray"
          },
          "& .MuiInput-underline:before": {
            borderColor: "darkgray",
            color: "darkgray"
          },
          "& .MuiInput-underline:after": {
            borderColor: "white",
            color: "darkgray"
          },
          "& .Mui-focused:after": {
            color: "darkgray",
            fontSize: '1.5vmin'
          },
          "& .MuiInputAdornment-root": {
            color: "darkgray",
          }
        }
      }
    },
    MuiTooltip: {
      styleOverrides: {
        tooltip: {
          fontSize: "1em",
          maxWidth: "1000px"
        },
      }
    }
  },
  palette: {
    mode: 'dark',
    primary: {
      main: '#95ef77'
    },
    secondary: {
      main: '#424cab'
    },
    success: {
      main: '#95ef77'
    },
    warning: {
      main: '#f2a365'
    },
    error: {
      main: '#ffffff'
    },
    info: {
      main: '#CDCED0'
    },
  },
});

const App: React.FC = () => {
  const [applications] = React.useState([
    {
      name: 'taskbar',
      render: <Taskbar />,
    },
    {
      name: 'killfeed',
      render: <KillFeed />,
    },
    {
      name: 'vehiclespawner',
      render: <VehicleSpawner />,
    }
  ]);

  return (
    <ThemeProvider theme={darkTheme}>      
      {applications.map((data)=>{
        return (
          <Application name={data.name} render={data.render} />
        )
      })}  
    </ThemeProvider>
  );
}

export default App;