import React, { useState, useEffect, useRef } from 'react';
import { useNuiEvent } from "../../../hooks/useNuiEvent";
import useStyles from './taskbar.styles';
import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';

const TaskBar: React.FC = () => {
    const classes = useStyles();

    const [ShowTaskBar, setShowTaskBar]: any = useState(false)
    const [TaskId, SetTaskId]: any = useState('')
    const [Label, SetLabel]: any = useState('')
    const [duration, SetDuration]: any = useState(0)
    const [Value, SetValue]: any = useState(25)



    useNuiEvent('uiMessage', (data) => {
      var sildata = data.data
      if ('taskbar' === data.app) {
        // if(sildata.show == null){
        //   setShowTaskBar(false)
        //   SetTaskId('')
        //   SetLabel('')
        //   SetDuration(0)
        //   SetValue(0)
        //   // return <DveXAlert AlertText='Error occurred in app: Taskbar - restarting...' AlertType='error' />
        // }

        if(void 0 !== sildata.update) {
          SetValue(sildata.value)
        }
        if(true === sildata.display){
          setShowTaskBar(true)
          SetTaskId(sildata.taskID)
          SetLabel(sildata.label)
          SetDuration(sildata.duration)
          SetValue(0)
        }else if(false === sildata.display){
          setShowTaskBar(false)
          SetTaskId('')
          SetLabel('')
          SetDuration(0)
          SetValue(0)
        }
      }
    })



    return (
      <>
        <div style={{display: ShowTaskBar ? '' : 'none'}} className={classes.taskbarOuterContainer}>
          <div className={classes.taskbarFlex}></div>
          <div className={classes.taskbarInnerContainer}>
            <div style={{ display: 'flex' }} className={classes.taskbarInnerContainer}>
              <div className={classes.taskbarNormalContainer}>
                <div className={classes.taskbarTextContainer}>
                  <p style={{ textAlign: 'center' }} className={classes.taskbarText}>{Label}</p>
                </div>
                <div className={classes.taskbarSliderContainer} style={{width: ''+Value+'%', transition: 'width 0ms linear 0s'}}></div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }

export default TaskBar;