import React, { useState, useEffect, useRef } from 'react';
import { useNuiEvent } from "../../../hooks/useNuiEvent";
import './killfeed.css';

const Killfeed: React.FC = () => {
  const [killLogs, setKillLogs] = useState<string[]>([]);

  useNuiEvent('uiMessage', ({ data, app }) => {
    //console.log(data.message,app)
    if (app === "killfeed") {
        addKillLog(data.message)
      }
    })

  const addKillLog = (message: string) => {
    setKillLogs((prevLogs) => [...prevLogs, message]);

    setTimeout(() => {
      setKillLogs((prevLogs) => prevLogs.slice(0, prevLogs.length - 1));
    }, 3000);
  };

  return (
    <div className="app">
      <div className="kill-log-container">
        {killLogs.map((log, index) => (
          <div key={index} className="kill-log visible">
            {/* <img src={log.weaponImage} alt="Weapon" width="20" height="20" style="margin-top: 2px;" /> */}
            {log}
          </div>
        ))}
      </div>

       {/* <button onClick={() => addKillLog('Player 1 got a kill')}>Add Kill Log 1</button>
      <button onClick={() => addKillLog('Player 2 died')}>Add Kill Log 2</button>
      <button onClick={() => addKillLog('Player 3 got a kill')}>Add Kill Log 3</button>  */}
    </div>
  );
};

export default Killfeed;